.CharCard {
  padding: 16px;
  border: 1px solid transparent;
}
.CharCard img{
  width:170px;
  height: 170px;
}
.CharCard:hover {
  background-color: #F7D66A;
  border: 1px solid #35A7FF;
  box-shadow: 0 1px 2px;
}

header.Jumbotron {
  background-color: #38618C;
  background-repeat: repeat;
  color: #fff;
  min-height: 50px;
  padding: 50px 20px;
  padding-bottom: 140px;
  text-align: center;
}
  
.Nav {
    width: 100%;
    font-size:1.5rem;
    background-color: #FF5964; 
    color:#38618C;
    font-weight: bold;   
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
.imgContainer {
  display: flex;
  flex-wrap: wrap;
  max-width: 900px;
  margin: 0 auto;
}
* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
    background-color: #EEEEEE;
  }
  
